        CollectionMoney:'',//
        PaymentName:'',//
        CollectionAcount:'',//
        PaymentAccount:'',//
        select:'',//
        PayPassword:'',//
    };
  },
  methods: {
      //
      again(){
        this.active = 0
              this.CollectionMoney = ''
          this.PaymentName = ''
              this.CollectionAcount = ''
              this.PaymentAccount = ''
              this.select = ''
              this.PayPassword = ''
      },
