          printJS('printJS-form', 'html')
          this.active = 0
          this.CollectionMoney = ''
          this.PaymentName = ''
          this.CollectionAcount = ''
          this.PaymentAccount = ''
          this.select = ''
          this.PayPassword = ''
      }
  },
  mounted() {}
};
</script>

<style scoped lang="scss">
    .margintop {
        margin-top: 20px;
        text-align: center;
    }
    .activebox{
        margin: auto auto;
    }
    .colorred {
        color: red;
        margin-right: 2px;
    }
</style>
